.social {
  font-size: 0;
}

.social__item {
  display: inline;
  margin-right: 6px;

  &--last-item {
    margin-right: 0;
  }
}

.social__button {
  width: 48px;
  height: 48px;
  padding: 0;

  &::before {
    content: " ";
    background-size: contain;
    margin: 12px;
    width: 24px;
    height: 24px;
    display: inline-block;
  }

  &--vkontakte::before {
    background-image: url("/img/social/vkontakte.svg");
  }

  &--vkontakte:active::before {
    background-image: url("/img/social/vkontakte_active.svg");
  }

  &--telegram::before {
    background-image: url("/img/social/telegram.svg");
  }

  &--telegram:active::before {
    background-image: url("/img/social/telegram_active.svg");
  }

  &--youtube::before {
    background-image: url("/img/social/youtube.svg");
  }

  &--youtube:active::before {
    background-image: url("/img/social/youtube_active.svg");
  }
}
